import React from "react";
import styled from "styled-components";

const Main = styled.div`
    background: green;
    height: 100%;
    width: 20%;
    padding: 0 15px;
`;
const Title = styled.div`
    height: 20%;
    width: 100%;
    P{
        font-size: 30px;
    }
`;

export default function index() {
    return (
        <Main>
            <Title><p>LINKS</p></Title>
        </Main>
    )
}